.contacts__container {
  @media screen and (max-width: 767px) {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.contacts {
  padding-top: 43px;
  padding-bottom: 57px;
  background-image: linear-gradient(to bottom, $white-color 8%, $bg-color 8%, $bg-color 100%);

  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 80px;
    background-image: linear-gradient(to bottom, $white-color 17%, $bg-color 17%, $bg-color 100%);
  }

  // bg_img_____________________
  @media screen and (min-width: 1280px) {
    padding-top: 60px;
    padding-bottom: 80px;

    background-image: url(../images/contacts/contacts-bg-desktop.png),
      linear-gradient(to bottom, $white-color 18%, $bg-color 18%, $bg-color 100%);

    background-position: 50% 33%;
    background-repeat: no-repeat;
    background-size: 100%;

    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/contacts/contacts-bg-desktop.png),
        linear-gradient(to bottom, $white-color 18%, $bg-color 18%, $bg-color 100%);
    }
  }

  &__list {
    margin-bottom: 54px;

    @media screen and (min-width: 768px) {
      display: flex;
      margin: -10px;
      margin-bottom: 80px;
    }

    @media screen and (min-width: 1280px) {
      margin: -15px;
      margin-bottom: 86px;
    }
  }

  &__item {
    padding: 43px 20px;
    background: $white-color;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;

    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1);

    // ____________________animation test___________________

    &:hover,
    &:focus {
      box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.3);
    }

    @media screen and (max-width: 767px) {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    @media screen and (min-width: 768px) {
      padding: 50px 30px;
      margin: 10px;
      width: calc((100% - 6 * 10px) / 3);
    }

    @media screen and (min-width: 1280px) {
      padding: 60px 44px;
      margin: 15px;
      width: calc((100% - 6 * 10px) / 3);
    }
  }

  &__title {
    font-size: 12px;
    line-height: 1.86;
    font-weight: 500px;
    color: $white-color;
    letter-spacing: 0.04em;

    margin-bottom: 25px;
    padding: 3px 25px;
    display: inline-block;
    text-align: center;
    border-radius: 4px;
    background-color: $accent-peach-color;

    @media screen and (min-width: 768px) {
      margin-bottom: 30px;
      font-size: 16px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 32px;
    }
  }

  &__title--mint {
    background-color: $accent-mint-color;
  }

  &__subtitle {
    font-size: 12px;
    line-height: 1.86;
    font-weight: 500px;
    color: $primary-text-color;
    letter-spacing: 0.04em;

    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      margin-bottom: 14px;
      font-size: 14px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 32px;
      font-size: 16px;
    }
  }

  &__text {
    font-size: 12px;
    line-height: 1.5;
    font-weight: 500px;
    color: $secondary-text-color;

    margin-bottom: 25px;

    @media screen and (min-width: 768px) {
      margin-bottom: 30px;
      font-size: 14px;
      line-height: 1.66;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 32px;
      font-size: 16px;
    }
  }

  &__days {
    padding: 25px 0;
    border-top: 1px solid $bg-hover-color-btn;
    border-bottom: 1px solid $bg-hover-color-btn;

    @media screen and (min-width: 768px) {
      padding: 30px 0;
    }

    @media screen and (min-width: 1280px) {
      padding: 32px 0;
    }
  }

  &__workdays {
    font-size: 12px;
    line-height: 1.86;
    font-weight: 500px;
    color: $primary-text-color;
    letter-spacing: 0.04em;

    @media screen and (min-width: 768px) {
      font-size: 14px;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }
  }

  &__time {
    font-size: 12px;
    line-height: 1.86;
    font-weight: 500px;
    color: $img-bg-color;

    @media screen and (min-width: 768px) {
      font-size: 14px;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }

    &:not(:last-child) {
      margin-bottom: 25px;

      @media screen and (min-width: 768px) {
        margin-bottom: 30px;
      }
    }
  }

  &__link {
    font-size: 12px;
    line-height: 1.5;
    font-weight: 500px;
    color: $secondary-text-color;

    display: block;
    &:hover,
    &:focus {
      color: $accent-color;
    }

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.83;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }
  }
}

.contacts__connection {
  padding-top: 25px;

  @media screen and (min-width: 768px) {
    padding-top: 30px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 32px;
  }
}

.contacts__communication {
  &:not(:last-child) {
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      margin-bottom: 14px;
    }
  }
}

.contacts__button-wrap {
  text-align: center;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.button__location {
  display: block;

  padding: 13px 17px;
  min-width: 161px;

  &--white {
    font-size: 16px;
    line-height: 1.31;
    font-weight: 700px;
    color: $white-color;
  }
  @media screen and (max-width: 767px) {
    margin: 0 auto;
    margin-bottom: 10px;
  }

  @media screen and (min-width: 768px) {
    min-width: 192px;
    padding: 13px 30px;
    margin-bottom: 0;
    margin-right: 15px;
    letter-spacing: 0.02em;
  }

  @media screen and (min-width: 1280px) {
    min-width: 198px;
    padding: 13px 34px;
    margin-bottom: 0;
    letter-spacing: 0.01em;
  }
}

.button__franchise {
  display: block;
  margin: 0 auto;
  min-width: 161px;
  padding: 13px 17px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;

  @media screen and (min-width: 768px) {
    min-width: 192px;
    padding: 13px 37px;
    margin: unset;
  }

  @media screen and (min-width: 1280px) {
    min-width: 198px;
    padding: 13px 39px;
  }
}
